.discount-button {
  display: inline-block;
  width: 100%;
  max-width: 222px;
  height: 34px;
  border: 1px solid #999;
  border-radius: 18px;

  &:hover {
    text-decoration: none;
    border-color: #7a7a7a;
  }

  &__content {
    text-align: center;
    .font--benton-sans-bold();
    font-size: 13px;
    line-height: 2.47;
    color: @black;

    .external-icon {
      display: inline-block;
      width: 12px;
      height: 12px;
      margin-left: @padding-xxs;
      margin-bottom: -2px; 
      background-image: url('../images/icons/external.svg');
      background-position: center;
      background-repeat: no-repeat;
      background-size: contain;
    }
  }
}