@mobile-width: 170px;

.package-detail {
  padding: @padding-xs;

  @media @mq-sm {
    padding: 40px @padding-sm;
  }

  @media @mq-md {
    padding: 50px @padding-sm;
  }

  &__discount {
    margin-top: @padding-lg;
    margin-bottom: @padding-lg;
    text-align: center;
  }

  &__wrapper {
    
    @media @mq-sm {
      padding-right: 8px;
    }

    @media @mq-md {
      padding-right: 40px;
    }

    //overwrite the standard .package class
    .package {
      margin-bottom: 0;
      border-left: 6px solid;
      cursor: auto;

      &:hover {
        box-shadow: 2px 2px 10px 0 rgba(0, 0, 0, 0.05);
        border-color: @di-grey-light;
      }

      &-content-wrapper {
        width: @mobile-width;
        padding-bottom: @padding-sm;
        border-bottom: none;

        @media @mq-sm {
          width: 100%;
          padding-left: @padding-sm;
          cursor: auto;
          border-bottom: 1px solid @di-grey-lighter;
        }

        @media @mq-md {
          padding-left: @padding-xxl;
          padding-right: @padding-xxl;
        }

        &:after {
          right: -5px;
          width: 14px;
          height: 7px;
          background-image: url('../images/icons/down.svg');
        }
      }

      .opened {
        &:after {
          background-image: url('../images/icons/up.svg');
        }
      }

      &-listing {
        padding-left: @padding-lg;
        padding-right: @padding-lg;

        @media @mq-sm {
          padding-left: @padding-sm;
          padding-right: @padding-md;
          cursor: auto;
        }

        @media @mq-md {
          padding-left: @padding-xxl;
          padding-right: @padding-xxl;
        }

        &__plus-divider {
          margin-left: -@padding-xs;
          margin-right: -@padding-xs;

          @media @mq-sm {
            margin-left: -@padding-sm;
            margin-right: -@padding-md;
          }

          @media @mq-md {
            margin-left: -@padding-xxl;
            margin-right: -@padding-xxl;
          }
        }
      }

      &__name {
        @media @mq-sm {
          width: 100%;
          text-align: center;
          font-size: 14px;
        }

        @media @mq-md {
          font-size: @medium;
        }
      }

      &__title {
        @media @mq-sm {
          margin-top: @padding-md;
        }
      }

      &-button {
        position: absolute;
        right: 20px;
        top: 34px;
        margin-top: 0;

        @media @mq-sm {
          position: static;
          display: inline-block;
          width: 100%;
          margin-top: 25px;
          text-align: center;
        }

        &__link {
          padding: @padding-sm;
          color: @di-blue;
          border: 0;
          text-transform: none;
          .font--benton-sans();
          font-size: @normal;
        }
        
      }
    }

    .di-digitalt {
      border-left-color: @di-bg-red;

      &:hover {
        border-left: 6px solid @di-bg-red;
      }
    }

    .di-total {
      border-left-color: @di-blue;

      &:hover {
        border-left: 6px solid @di-blue;
      }
    }

    .di-helg {
      border-left-color: @di-blue-lighter;

      &:hover {
        border-left: 6px solid @di-blue-lighter;
      }
    }

    .di-total, .di-helg {
      .package-listing__divider, .package-listing__detail--base {
        display: block;

        @media @mq-sm {
          display: none;
        }
      }
    }
  }

  &__iframe {
    @media @mq-md {
      padding-left: 38px;
    }
  }
}