.compare-package-mobile {
  .package-compare {
    margin-top: @padding-md;
    margin-bottom: @padding-md;

    &__title {
      .font--benton-sans-bold();
      font-size: 16px;
      letter-spacing: -0.1px;
      color: @black-2;
    }

    &__detail {}

    &__listing {
      padding-left: @padding-lg;
      list-style: url('../images/icons/check-usp-medium.svg');
    }

    &__item {
      position: relative;
      margin-top: @padding-md;
      .font--helvetica-neue();
      font-size: @small;
      line-height: 1.23;
      letter-spacing: -0.3px;
      color: @black;

      .item-text {
        position: absolute;
        top: 1px;
      }
    }

    &__button {
      margin-top: 25px;
      margin-bottom: 40px;
      text-align: center;

      &-link {
        display: inline-block;
        width: 200px;
        height: 44px;
        padding: 14px 35px;
        border-radius: 2px;
        border: 1px solid;
        .font--benton-sans-bold();
        font-size: 14px;
        line-height: 1;
        text-align: center;
        text-transform: uppercase;
        color: @white;

        &--digitalt {
          border-color: @di-bg-red;
          background-color: @di-bg-red;
        }

        &--total {
          border-color: @di-blue;
          background-color: @di-blue;
        }

        &--helg {
          border-color: @di-blue-lighter;
          background-color: @di-blue-lighter;
        }
      }
    }
  }
}

.compare-package-desktop{
  margin-top: @padding-md;
  padding-left: @padding-lg;
  padding-right: @padding-lg;

  @media @mq-md {
    margin-top: @padding-lg;
  }

  .package-compare {
    .row {
      display: flex;
      height: 50px;
      line-height: 50px;
      border: 1px solid @di-pink;

      .col-sm-6 {
        width: 58%;
      }

      .col-sm-2 {
        width: 14%;
        padding: 0;
        text-align: center;
      }
    }

    .row + .row {
      border-top:0 ;
    }

    .row:last-child {
      border: 0;
    }

    .col-sm-6 {
      border-right: 1px solid @di-pink;
    }

    .col-sm-2 + .col-sm-2 {
      border-left: 1px solid @di-pink;
    }

    &__header {
      .font--benton-sans-bold();
      font-size: 16px;
      letter-spacing: -0.1px;
      text-align: center;
      color: @black-2;
    }

    &__description {
      padding-left: @padding-xs;
      .font--helvetica-neue();
      font-size: @normal;
      letter-spacing: 0.1px;
      color: @black;

      @media @mq-md {
        padding-left: 70px;
      }
    }

    &__check {
      margin-top: @padding-sm;
      display: inline-block;
      width: 20px;
      height: 20px;
      background-image: url('../images/icons/check-usp-large.svg');
    }

    &__button {
      display: flex;
      text-align: center;
      border: 0 !important;

      @media @mq-md {
        padding-top: @padding-md;
        padding-bottom: @padding-md;
      }

      &-link {
        width: 100%;
        height: 40px;
        padding: 6px @padding-xs;
        margin: @padding-xs @padding-xxs;
        .font--benton-sans-bold();
        font-size: 10px;
        text-align: center;
        text-transform: uppercase;
        color: @white;
        border: 1px solid;
        border-radius: 2px;
        cursor: pointer;

        @media @mq-md {
          font-size: 12px;
        }

        @media only screen and (min-width: 1200px) {
          padding: @padding-xs @padding-xxs;
          margin: @padding-md @padding-xxs;
          font-size: 14px;
        }

        &:hover {
          text-decoration: none;
        }

        .text {
          line-height: 1.3;
        }
      }

      .di-digitalt {
        border-color: @di-bg-red;
        background-color: @di-bg-red;

        &:hover {
          border-color: darken(@di-bg-red, 10%);
          background-color: darken(@di-bg-red, 10%);
        }
      }

      .di-total {
        border-color: @di-blue;
        background-color: @di-blue;

        &:hover {
          border-color: darken(@di-blue, 10%);
          background-color: darken(@di-blue, 10%);
        }
      }

      .di-helg {
        border-color: @di-blue-lighter;
        background-color: @di-blue-lighter;

        &:hover {
          border-color: darken(@di-blue-lighter, 10%);
          background-color: darken(@di-blue-lighter, 10%);
        }
      }
    }
  }
}