.header {
  height: 44px;
  background-color: @di-bg-red-dark;

  &__content {
    height: 44px;
    padding: 7px;
  }

  &__logo {
    display: inline-block;
    width: 34px;
    height: 30px;
    background-image: url('../images/icons/di-logo-white.png');
    background-position: center;
    background-repeat: no-repeat;
    background-size: contain;
  }
}