@typo-weight-light: 200;
@typo-weight-regular: 400;
@typo-weight-bold: 400;
@typo-weight-black: 400;

.typo-font-family(@font-family) {
    @font-fallback: "Helvetica Neue", Helvetica, Arial, sans-serif;
    font-family: '@{font-family}', @font-fallback;
}

.typo-load-font-face(@font-family, @font-file-name) {
    @font-full-path: "@{FONT_PATH}@{font-file-name}";

    @font-face {
      font-family: '@{font-family}';
      src: url('@{font-full-path}.eot'); /* IE9 Compat Modes */
      src: url('@{font-full-path}.eot?#iefix') format('embedded-opentype'), /* IE6-IE8 */
           //url('webfont.woff2') format('woff2'), /* Super Modern Browsers */
           url('@{font-full-path}.woff') format('woff'), /* Pretty Modern Browsers */
           url('@{font-full-path}.ttf')  format('truetype'), /* Safari, Android, iOS */
           url('@{font-full-path}.svg#svgFontName') format('svg'); /* Legacy iOS */
    }
}

.typo-load-font-face(BentonSans, "BentonSans-Regular");
.typo-load-font-face(BentonSansBlack, "BentonSans-Black");
.typo-load-font-face(BentonSansBold, "BentonSans-Bold");
.typo-load-font-face(HelveticaNeue, "HelveticaNeueDeskInterface-Regular");

.font--benton-sans {
    .typo-font-family(BentonSans);
    font-weight: @typo-weight-regular;
    font-style: normal;
	font-stretch: normal;
}
.font--benton-sans-black {
    .typo-font-family(BentonSansBlack);
    font-weight: @typo-weight-regular;
    font-style: normal;
	font-stretch: normal;
}
.font--benton-sans-bold {
    .typo-font-family(BentonSansBold);
    font-weight: @typo-weight-regular;
    font-style: normal;
	font-stretch: normal;
}
.font--helvetica-neue {
    .typo-font-family(HelveticaNeue);
    font-weight: @typo-weight-regular;
    font-style: normal;
	font-stretch: normal;
}