.package {
  background-color: @white;
  box-shadow: 2px 2px 10px 0 rgba(0, 0, 0, 0.05);
  border: solid 1px @di-grey-light;
  padding-top: @padding-xs;
  // padding-bottom: @padding-sm;
  border-left-width: 6px;
  margin-bottom: @padding-md;
  height: 100%;
  cursor: pointer;

  @media @mq-sm {
    padding-top: @padding-sm;
    // padding-bottom: 25px;
    border-left: 1px solid;
    
    &:hover {
      box-shadow: 2px 2px 20px 0 rgba(0, 0, 0, 0.1);
      border: 1px solid @di-pink;
    }
  }

  @media @mq-md {
    padding-top: @padding-lg;
    // padding-bottom: @padding-lg;
  }

  &-content-wrapper, &-listing {
    padding-left: @padding-sm;
    padding-right: @padding-sm;
   
    @media @mq-sm {
      padding-left: @padding-md;
      padding-right: @padding-md;
    }

    @media @mq-md {
      padding-left: 50px;
      padding-right: 50px;
    }
  }

  &-content-wrapper {
    position: relative;
    padding-bottom: @padding-md;
    border-bottom: 1px solid @di-grey-lighter;

    &:after {
      position: absolute;
      top: 40%;
      right: 20px;
      content: " ";
      width: 7px;
      height: 14px;
      background-image: url('../images/icons/right.svg');
      background-repeat: no-repeat;

      @media @mq-sm {
        display: none;
      }
    }
  }

  &__name {
    .font--benton-sans-bold();
    font-size: @small;
    line-height: 1;
    color: @black;
    text-transform: uppercase;
  }

  &__title {
    margin-top: @padding-xs;
    .font--benton-sans();
    font-size: @normal;
    line-height: 1.33;
    letter-spacing: 0.2px;
    color: @black;

    @media @mq-sm {
      margin-top: 0;
      font-size: 16px;
      line-height: 1.31;
      letter-spacing: normal;
      text-align: center;
      color: @black-1;
    }

    @media @mq-md {
      font-size: 20px;
      line-height: 1.25;
    }
  }

  &__img {
    margin-top: @padding-md;
    width: 100%;
    height: 100px;
    background-position: center;
    background-repeat: no-repeat;
    background-size: contain;

    @media @mq-md {
      height: 140px;
    }

    &--digitalt {
      margin-top: 40px;
      background-image: url('../images/img-digitalt.png');

      @media @mq-md {
        margin-top: @padding-xl;
      }
    }

    &--total {
      background-image: url('../images/img-dagstidning.png');
    }

    &--helg {
      background-image: url('../images/img-helg.png');
    }
  }

  &__button {
    margin-top: @padding-md;

    @media @mq-md {
      margin-top: @padding-sm;
    }

    &-link {
      display: inline-block;
      height: 40px;
      width: 100%;
      border: 1px solid;
      border-radius: 2px;
      .font--benton-sans-bold();
      font-size: @small;
      line-height: 40px;
      color: @white;
      text-align: center;
      text-transform: uppercase;
      cursor: pointer;

      &:hover {
        color: @white;
        text-decoration: none;
      }
    }
  }

  &__pricing {
    margin-top: @padding-xs;
    .font--benton-sans();
    font-size: @normal;
    line-height: 1.2;
    color: @black-1;

    @media @mq-sm {
      font-size: 12px;
      line-height: 1.75;
      color: @black;
      text-align: center;
    }
  }

  &-listing {
    border-top: 1px solid @di-grey-lighter;
    background-color: #f9f9f9;
    cursor: auto;
    margin-left: -6px;
    margin-bottom: -1px;
    padding-bottom: 15px;

    @media @mq-sm {
      border-top: 0;
      background-color: @white;
      cursor: pointer;
      margin-left: 0;
      margin-bottom: 0;
      padding-bottom: 0;
    }

    &__title {
      margin-top: @padding-lg;
      .font--benton-sans();
      font-size: 18px;
      line-height: 1.17;
      color: @di-blue-light;
      text-align: center;

      @media @mq-sm {
        margin-top: @padding-md;
      }
    }

    &__divider {
      margin-top: @padding-xs;
      .font--benton-sans();
      font-size: @normal;
      line-height: 1.33;
      letter-spacing: 0.2px;
      text-align: center;
      color: @di-grey;
      margin-bottom: @padding-xs;

      @media @mq-sm {
        opacity: 0.8;
        font-size: @small;
        line-height: 1.54;
        margin-bottom: 0;
      }

      @media @mq-md {
        margin-left: -@padding-xxs;
        margin-right: -@padding-xxs;
      }
    }

    &__detail {
      padding-left: @padding-md;
      margin-left: -3px;
      list-style: url('../images/icons/check-usp-small.svg');
    }

    &__item {
      padding-top: @padding-md;
      .font--benton-sans();
      font-size: @normal;
      line-height: 1.33;
      letter-spacing: 0.2px;
      color: @di-grey;

      @media @mq-sm {
        font-size: @small;
        line-height: 1.54;
      }
    }

    &__plus {
      .font--benton-sans();
      font-size: 18px;
      line-height: 1.17;
      text-align: center;
      color: @di-blue-light;
    }

    &__plus-divider {
      position: relative;
      height: 1px;
      margin-top: @padding-lg;
      margin-bottom: @padding-lg;
      background-color: @di-grey-lighter;

      @media @mq-sm {
        margin-top: @padding-md;
        margin-left: -@padding-md;
        margin-right: -@padding-md;
      }

      @media @mq-md {
        margin-left: -50px;
        margin-right: -50px;
      }

      .icon-plus {
        position: absolute;
        top: -5px;
        left: 0;
        right: 0;
        margin: auto;
        width: 80px;
        height: 10px;
        background-color: #f9f9f9;
        background-image: url('../images/icons/plus.svg');
        background-position: center;
        background-repeat: no-repeat;
        background-size: contain;

        @media @mq-sm {
          width: 60px;
          background-color: @white;
        }
      }
    }

    .package__button {
      border-top: 1px solid @di-grey-lighter;
      text-align: center;

      &-link {
        margin-top: @padding-lg;
        width: 220px;
      }
    }

    .package__pricing {
      text-align: center;
      font-size: @small;
      margin-bottom: @padding-xs;
    }
  }

  &-view-all {
    height: 44px;

    &__link {
      display: inline-block;
      width: 100%;
      height: 44px;
      text-align: center;

      &:hover {
        text-decoration: none;
        
        .package-listing {
          display: block !important;
        }
      }

      .text {
        .font--benton-sans-bold();
        font-size: 14px;
        line-height: 44px;
        letter-spacing: 0.2px;
        color: @di-blue;
      }

      .icon {
        display: inline-block;
        margin-left: 8px;
        width: 12px;
        height: 8px;
        background-position: center;
        background-repeat: no-repeat;
      }

      .icon-down {
        background-image: url('../images/icons/down-blue.svg');
      }

      .icon-up {
        background-image: url('../images/icons/up-blue.svg');
      }
    }
  }

  &--di-digitalt {
    border-left-color: @di-bg-red;

    @media @mq-sm {
      border-left-color: @di-grey-light;
      &:hover {
        .package__button-link {
          background-color: darken(@di-bg-red, 10%);
          border-color: darken(@di-bg-red, 10%);
        }  
      }
    }

    .package__button-link {
      background-color: @di-bg-red;
      border-color: @di-bg-red;

      &:hover {
        background-color: darken(@di-bg-red, 10%);
        border-color: darken(@di-bg-red, 10%);
      }
    }
  }

  &--di-total {
    border-left-color: @di-blue;

    @media @mq-sm {
      border-left-color: @di-grey-light;

      &:hover {
        .package__button-link {
          background-color: darken(@di-blue, 10%);
          border-color: darken(@di-blue, 10%);
        }  
      }
    }

    .package__button-link {
      background-color: @di-blue;
      border-color: @di-blue;

      &:hover {
        background-color: darken(@di-blue, 10%);
        border-color: darken(@di-blue, 10%);
      }  
    }
  }

  &--di-helg {
    border-left-color: @di-blue-lighter;

    @media @mq-sm {
      border-left-color: @di-grey-light;
      &:hover {
        .package__button-link {
          background-color: darken(@di-blue-lighter, 10%);
          border-color: darken(@di-blue-lighter, 10%);
        }  
      }
    }

    .package__button-link {
      background-color: @di-blue-lighter;
      border-color: @di-blue-lighter;

      &:hover {
        background-color: darken(@di-blue-lighter, 10%);
        border-color: darken(@di-blue-lighter, 10%);
      }
    }
  }
}