.main {
  padding-left: @padding-md;
  padding-right: @padding-md;

  @media @mq-sm {
    padding-left: @padding-sm;
    padding-right: @padding-sm;
  }

  &__header {
    position: relative;
    margin-top: @padding-xs;
    margin-bottom: @padding-md;

    @media @mq-sm {
      margin-top: @padding-md;
      margin-bottom: @padding-lg;
    }

    @media @mq-md {
      margin-top: @padding-lg;
    }

    &-discount {
      margin-top: @padding-md;
      text-align: center;

      @media @mq-md {
        text-align: left;
        margin-top: 3px;
      }
    }

    &-compare {
      position: absolute;
      top: 0;
      right: 0;

      .compare-package-link {
        float: right;
        padding: 13px 25px;
        .font--benton-sans-bold();
        font-size: 11px;
        color: #2a6c9f;

        @media @mq-md {
          font-size: @normal;
        }

        &:hover {
          text-decoration: none;
          background-color: #e9ddd7;
        }
      }
    }

    &-title {
      .font--benton-sans();
      text-align: center;

      .header-title {
        font-size: 22px;
        line-height: 1.82;
        letter-spacing: -0.7px;
        color: @black-1;

        @media @mq-sm {
          font-size: @large;
          line-height: 1.54;
        }

        @media @mq-md {
          font-size: @huge;
          line-height: 1.33;
        }
      }

      .header-sub-title {
        font-size: @normal;
        line-height: 1.18;
        letter-spacing: 0.2px;
        color: @di-grey;

        @media @mq-sm {
          margin-top: @padding-xs;
        }

        @media @mq-md {
          font-size: @medium;
        }
      }
    }
  }

  @padding-sm-1: 8px;
  @padding-sm-2: 12px;
  @padding-md-1: 42px;
  @padding-md-2: 28px;
  &__package {
    &-wrapper {
      @media @mq-sm {
        display: flex;
      }
    }

    .package-1 {
      @media @mq-sm {
        padding-right: @padding-sm-1;
      }

      @media @mq-md {
        padding-right: @padding-md-1;
      }
    }

    .package-2 {
      @media @mq-sm {
        padding-left: @padding-sm-2;
        padding-right: @padding-sm-2;
      }

      @media @mq-md {
        padding-left: @padding-md-2;
        padding-right: @padding-md-2;
      }
    }

    .package-3 {
      @media @mq-sm {
        padding-left: @padding-sm-1;
      }

      @media @mq-md {
        padding-left: @padding-md-1;
      }
    }
  }

  &__compare-package {
    margin-top: 40px;
    margin-bottom: @padding-xxl;

    @media @mq-sm {
      margin-top: @padding-xxl;
    }

    .compare-package {
      &__title {
        .font--benton-sans();
        font-size: 20px;
        line-height: 1.2;
        letter-spacing: -0.5px;
        text-align: center;
        color: @black-1;

        @media @mq-sm {
          line-height: 1.6;
          letter-spacing: -0.8px;
        }

        @media @mq-md {
          font-size: @large;
          line-height: 1.23;
        }
      }
    }
  }
}